export default function CheckBoxTemplate(selectCheckBoxLabel) {
  var _checked = 'data:image/png;base64,',
      _unchecked = 'data:image/png;base64,';

  function template() {
    return {};
  }

  function getHashCode() {
    return 0;
  }

  function render(doc, position, data) {
    var image = data.isSelected ? _checked : _unchecked;
    doc.save();
    /* photo */

    doc.image(image, position.x, position.y);
    doc.font('Helvetica', 11).text(selectCheckBoxLabel, position.x + 20, position.y + 4, {
      ellipsis: true,
      width: position.width - 4,
      height: position.height,
      align: 'left'
    });
    doc.restore();
  }

  return {
    template: template,
    getHashCode: getHashCode,
    render: render
  };
}
;